* {
  box-sizing: border-box;
}

body,div,span,applet,object,iframe,h1,h2,h3,h4,h5,h6,p {
  margin: 0;
  height: 100;
  font-family: Ubuntu, sans-serif;
}/* --------- CORE ---------  */body {
  font-family: Ubuntu, sans-serif;
}/* amarillo *//* gris claro *//* negro *//* gris oscuro *//* --------- COMPONENTS ---------  *//* FONT FAMILY *//* FONT SIZE *//* FONT WEIGHT */.submit-input {
  background-color: #ffcd49;
  color: #000;
  cursor: pointer;
  font-size: 1.125rem;
  font-weight: 700;
}/* --------- LAYOUT /* --------- *//*BASIC CALCULATOR STYLE (text and box parameters) */.calculator {
  text-align: center;
}

.info-calculator {
  margin-bottom: 10%;
}

.tertiary-title {
  font-size: 2rem;
  color: #ffcd49;
}

.fourth-title {
  font-size: 1.125rem;
  color: #000;
}

.calc-subtitle {
  font-weight: 500;
  margin-top: 5%;
  margin-top: 0;
  margin-bottom: 2%;
}

.about-calculator-function {
  display: none;
}/* BACKGROUND CALCULATOR STYLE */.form-calculator {
  background-color: #f0f0f0;
}/* FORM CALCULATOR STYLE */.form-calculator {
  padding-top: 10%;
  padding-bottom: 7%;
}

form {
  display: flex;
  justify-content: center;
  flex-direction: column;
}

input {
  width: 50%;
  margin-bottom: 2.5%;
  margin-top: 2%;
  padding: 15px 20px;
  align-self: center;
  text-align: center;
  border: 1px solid #bbbbbb;
}

input::placeholder {
  color: #bbbbbb;
  text-transform: uppercase;
}/* ANSWER STYLE */.answer-calculator {
  padding-top: 8%;
}

.salary-value-title {
  padding-top: 4%;
}

.accordion {
  background-color: #ffcd49;
  color: #000;
  cursor: pointer;
  padding: 20px;
  width: 100%;
  border: none;
  border-bottom: 1.5px solid #000;
  text-align: left;
  outline: none;
  font-size: 1.125rem;
  font-weight: 500;
  transition: 0.4s;
}

.accordion:hover {
  background-color: #F6AE2D;
}

.panel {
  padding: 0 18px;
  background-color: white;
  max-height: 0;
  overflow: hidden;
  transition: max-height 0.2s ease-out;
}

.title-slider {
  text-align: center;
  background-color: white;
  padding: 70px;
}

.testimony-title {
  font-size: 2rem;
}

li {
  list-style: none;
}

.testimonys {
  background-color: #f0f0f0;
  padding: 14%;
  margin: 0;
}/*.hidden {
  display: none;
}*/.testimony {
  background-color: white;
  width: 100%;
  padding: 20px;
}

.testimony .hidden {
  display: inline-block;
}

.testimony-person {
  background-color: #f0f0f0;
  padding: 30px;
  text-align: center;
}/*.hidden {
  display: none;*/@media all and (min-width: 768px) {
  .info-calculator {
    margin-bottom: 5%;
  }

  .calc-subtitle {
    margin-top: 3%;
  }

  .form-calculator {
    padding-top: 5%;
    padding-bottom: 3%;
  }

  input {
    width: 30%;
    margin-bottom: 1%;
    margin-top: 0.5%;
  }

  .answer-calculator {
    padding-top: 3.5%;
  }

  .slider {
    margin-bottom: 10%;
  }

  .title-slider {
    background-color: #f0f0f0;
    margin-top: 8%;
    padding: 50px;
  }

  .testimony-title {
    margin-bottom: 5%;
  }

  .testimonys {
    display: flex;
    padding: 2% 14%;
  }

  .testimony {
    width: 95%;
  }

  .testimony .hidden {
    display: inline-block;
  }

  .testimony-person {
    text-align: right;
    padding: 30px 0;
    width: 95%;
  }

  .testimony-person .hidden {
    display: inline-block;
  }
}

@media all and (min-width: 1024px) {
  .info-calculator {
    margin-bottom: 3%;
  }

  .calc-subtitle {
    margin-top: 2%;
  }

  .about-calculator-function {
    display: inline-block;
  }

  .form-calculator {
    display: flex;
    justify-content: center;
  }

  input {
    width: 120%;
    margin-bottom: 5%;
    margin-top: 4.5%;
  }

  .answer-calculator {
    margin-left: 15%;
  }

  .salary-value-title {
    padding-top: 10%;
  }

  .slider {
    background-color: #f0f0f0;
    margin-bottom: 10%;
  }

  .title-slider {
    background-color: #f0f0f0;
    margin-top: 8%;
    padding: 50px;
  }

  .testimony-title {
    margin-bottom: 3%;
  }

  .testimonys {
    display: flex;
    padding: 2% 14%;
  }

  .testimony {
    width: 95%;
  }

  .testimony-person {
    text-align: right;
    padding: 30px 0;
    width: 95%;
  }

  .testimony-person .hidden {
    display: inline-block;
  }
}
